.App {
  text-align: center;
}

.video_wrapper {
  position: relative;
  padding-bottom: 56.25%; /* 16:9, for an aspect ratio of 1:1 change to this value to 100% */
  display: block;
  width: 100%;
}

iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#error-page {
  display: flex;
  flex-direction: column;
  height: 90vh;
  justify-content: center;
  align-items: center;
}

.profile-picture-container {
  max-width: 90%;
  height: fit-content;
}

.profile-picture {
  max-width: 100%;
  max-height: 110vh;
}

@media (max-width: 991.98px) {
  .profile-picture-container {
    max-width: 75%;
    height: fit-content;
  }
}
